<template>
  <div class="mobile-container">
    <Header/>

    <div class="m-content">
      <div class="m-pos-bg">
        <img src="@/assets/m/bg.png" alt="" class="bg-img">
        <div class="m-pos-con">
          <div class="pos-con-img"><img src="@/assets/img/qr.png" alt="">{{ $t('lang.token_title7') }} </div>
          <div class="m-nodeid">{{ address_gkc }}</div>
        </div>
      </div>
      <div class="m-head">
        <div class="m-item">
          <div class="item-lebel">
            <span v-if="img !== ''">{{ img }}</span>
            <span>{{ value }}</span>
          </div>
          <p>{{ $t('lang.token_title1') }}</p>
          <router-link tag="a" :to="{path:'/searchone',query:{address: address }}"><img src="@/assets/img/arrow.png" alt="">
          </router-link>
        </div>
        <div class="m-item">
          <div class="item-lebel">{{ tokenData.assets }}  {{ tokenData.symbol }}</div>
          <p>{{ $t('lang.token_title3') }}</p>
        </div>
<!--        <div class="m-item">-->
<!--          <div class="item-lebel">{{ token }}</div>-->
<!--          <p>-->
<!--            Contract address-->
<!--          </p>-->
<!--        </div>-->
        <div class="m-item">
          <div class="item-lebel">{{ tokenData.totalSupply }} {{ tokenData.symbol }}</div>
          <p>
            <img src="@/assets/img/assets.png" alt="">
            {{ $t('lang.token_title4') }}
          </p>
        </div>
        <div class="m-item">
          <div class="item-lebel" style="font-size: 16px;">{{ filterDate(tokenData.timestamp) }}</div>
          <p>{{ $t('lang.token_title5') }}</p>
        </div>
      </div>
      <div class="m-tips">
        {{ $t('lang.token_title6') }}
      </div>
      <div>
        <el-table
          :data="tableData"
          style="">
          <el-table-column
            prop="time"
            :label="$t('lang.token_table1')"
            width="220">
            <template slot-scope="scope">
              {{ filterDate(scope.row.time) }}
            </template>
          </el-table-column>
          <el-table-column
            prop="from"
            :label="$t('lang.token_table2')"
            width="230">
            <template slot-scope="scope">
              <router-link tag="a" :to="{path:'/maddress',query:{address: scope.row.from_gkc }}">{{ subStr(scope.row.from_gkc) }}
              </router-link>
            </template>
          </el-table-column>
          <el-table-column
            prop="to"
            :label="$t('lang.token_table3')"
            width="230">
            <template slot-scope="scope">
              <img src="@/assets/img/out.png" style="width: 32px;height: 32px;vertical-align: middle;position: absolute;left: -27px;top: 6px;" alt="" v-if="scope.row.from_gkc == address_gkc">
              <img src="@/assets/img/in.png" style="width: 32px;height: 32px;vertical-align: middle;position: absolute;left: -27px;top: 6px;" alt="" v-else-if="scope.row.to_gkc == address_gkc">
              <router-link tag="a" :to="{path:'/address',query:{address: scope.row.to_gkc }}">{{ subStr(scope.row.to_gkc) }}
              </router-link>
            </template>
          </el-table-column>
          <el-table-column
            prop="value"
            :label="$t('lang.token_table4')"
            width="180">
            <template slot-scope="scope">
              {{ scope.row.value }} {{ tokenData.symbol }}
            </template>
          </el-table-column>
          <el-table-column
            prop="txid"
            :label="$t('lang.token_table5')"
            width="210">
            <template slot-scope="scope">
              <router-link tag="a" :to="{path:'/tokendetail',query:{txid: scope.row.txid }}">{{ subStr(scope.row.txid) }}
              </router-link>
            </template>
          </el-table-column>
          <el-table-column
            prop="block"
            :label="$t('lang.token_table6')"
            width="">
            <template slot-scope="scope">
              <router-link tag="a" :to="{path:'/block',query:{height: scope.row.block }}">{{ scope.row.block }}
              </router-link>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <div class="block_page">
        <pageSize :page-data="pageData" :layout="`prev, pager, next,jumper`" @currentChange="currentChange"
                  @sizeChange="sizeChange"/>
      </div>
    </div>
    <Footer/>
  </div>
</template>

<script>
  import Header from '@/components/MHeader'
  import Footer from '@/components/MFooter'
  import pageSize from '@/components/MPage'
  import dateFns from 'date-fns'

  export default {
    components: {
      Header,
      Footer,
      pageSize
    },
    data() {
      return {
        tableData: [],
        pageData: {
          currentPage: 1,
          pageSize: 10,
          total: 0
        },
        options: [],
        value: '',
        token: "",
        token_select: "",
        address: "",
        address_gkc: "",
        tokenData: {},
        img: ""
      }
    },
    created() {
      this.address = this.$route.query.address;
      this.token_select = this.$route.query.token;
    },
    computed: {},
    mounted() {
      this.getTokenList()
    },
    methods: {
      sizeChange(size) {
        this.pageData.currentPage = 1;
        this.pageData.pageSize = size
      },
      currentChange(val) {
        this.pageData.currentPage = val;
        this.getUserTokenList()
      },
      getTokenList() {
        this.$http.get("api/contract/token").then(res => {
          if (res.data.code === 200) {
            this.options = res.data.data.data
            this.options.sort(function(x, y){
              return x.name.localeCompare(y.name);
            });
            this.value = this.options[0].name;
            if (this.token_select === undefined) {
              this.token = this.options[0].token;
            }else{
              this.token = this.token_select
            }
            this.img = this.value.substring(0,1)
            this.getUserTokenList()
          }
        })
      },
      getUserTokenList() {
        this.$http.post("api/contract/usertoken", {address: this.address, token: this.token, page: this.pageData.currentPage}).then(res => {
          if (res.data.code === 200) {
            this.tableData = res.data.data.data
            this.tokenData = res.data.data.token
            this.address_gkc = res.data.data.address
            this.value = res.data.data.token.name;
            this.img = this.tokenData.name.substring(0,1)
            this.pageData.total = res.data.data.page.total
            this.pageData.pageSize = res.data.data.page.page_size
            this.pageData.currentPage = res.data.data.page.current_page
          }
        })
      },
      selectChange(val){
        this.pageData.currentPage = 1
        this.token = val
        this.getUserTokenList()
      },
      filterDate(value, type) {
        if (!value) return ''
        if (value > 0) {
          value = value * 1000
        }
        const filterType = type || 'YYYY-MM-DD HH:mm:ss Z'
        return dateFns.format(value, filterType)
      },
      subStr(str){
        if (str === "") {
          return ""
        }
        str = str.substr(0, 12);
        str += "...";
        return str
      }
    }
  }


</script>


<style scoped>
  .m-content {
    padding: 1rem;
    background: #E6E9EE;
  }
  .m-pos-bg{
    text-align: left;
    position: relative;
    margin-bottom: .8rem;
  }
  .m-pos-bg .bg-img{
    width: 100%;
    height: 8rem;
  }
  .m-pos-con{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    color: #ffffff;
  }
  .pos-con-img{
    margin: 1rem;
    display: flex;
    align-items: center;
  }
  .pos-con-img img{
    width: 2rem;
    height: 2rem;
    margin-right: .8rem;
  }
  .m-head {
    background: #ffffff;
  }

  .m-head .m-item {
    text-align: center;
    border-bottom: 1px solid #F0F0F0;
    padding: 1rem 0;
    position: relative;
  }

  .m-item p {
    font-size: 12px;
    font-weight: 400;
    color: rgba(136, 136, 136, 1);
    opacity: 1;
    vertical-align: middle;
  }

  .m-item p img {
    width: 24px;
    height: 24px;
    vertical-align: middle;
  }
  .m-item a img {
    width: 24px;
    height: 24px;
    vertical-align: middle;
    position: absolute;
    top: 13px;
    right: 5px;
  }

  .item-lebel {
    font-size:18px;
    font-weight:400;
    color:rgba(0,0,0,1);
    opacity:1;
    margin-bottom: .5rem;
    display: flex;
    justify-content: center;
    word-break: break-all;
    padding: 0 10px;
  }
  .item-lebel span {
    vertical-align: middle;
  }
  .item-lebel span:first-child {
    width: 18px;
    height: 18px;
    background-color: #0E932E;
    border-radius: 18px;
    display: inline-block;
    text-align: center;
    line-height: 18px;
    color: #ffffff !important;
    font-size: 12px !important;
    margin-right: 10px;
  }
  .item-lebel img:first-child {
    width: 18px;
    height: 18px;
    vertical-align: middle;
  }
  .item-lebel img:last-child {
    width: 24px;
    height: 24px;
    vertical-align: middle;
    float: right;
    margin-right: 15px;
  }
  .m-nodeid{
    word-wrap:break-word;
    margin: 0 5rem 0 3rem;
  }

  .m-tips {
    padding: .7rem;
    text-align: left;
  }
</style>



